body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;

  /* background: rgb(0, 0, 47); */
  background-color: #0E6E05;
  height: 100vh;

  display: -ms-flexbox;

  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

* {
  font-family: Helvetica, sans-serif;
  font-weight: 300;
}

.App {
  background-color: #fefefea4;
  padding: 20px;
  margin: 10px auto;
  border-radius: 10px;
  max-width: 320px;
}
@media screen and (max-width: 320px){
  .App {
    border-radius: 0;
  }
}

.form-group {
  margin: 10px 0;
}

.form-group > input, .form-group > span, .form-group > label {
  font-size: 1.5rem;
}

.form-group > label {
  font-weight: bold;
}

.text-center {
  text-align: center;
}

.form-group > input {
  padding: 5px 10px;
  border-radius: 5px;
  border: 1px solid #999;
  max-width: 120px;
}

.inverter {
  text-decoration: underline;
  cursor: pointer;
}

.bold {
  font-weight: bold;
}

.flex-center {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}


